<template>
  <div>
      <Loading :loading="loading"/>
      <div class="main" v-if="!loading && JSON.stringify(data) !== '{}'">
          <h1>{{data.title}}</h1>
          <div class="pay-mt" v-if="data.status === 1">
              <van-button type="primary" @click="onPay(2)">支付宝</van-button>
              <van-button type="primary" @click="onPay(1)">微信支付</van-button>
              <van-button type="primary" @click="onFind()">查询</van-button>
          </div>
      </div>
  </div>
</template>
<script>
import http from '@/utils/axios'
import func from '@/utils/func'
import wx from 'weixin-jsapi'
import { Toast } from 'vant'
import Loading from '@/components/common/Loading'
export default {
    data () {
        return {
            loading: false,
            id: '',
            data: {}
        }
    },
    components: {
        Loading
    },
    methods: {
        fetch () {
            if (this.loading) { return }
            this.loading = true
            http.ajaxGet('order/' + this.id, {}, res => {
                this.loading = false
                if (res.status === 'success') {
                    this.data = res.data
                } else {
                    Toast(res.message)
                }
            })
        },
        // eslint-disable-next-line camelcase
        onPay (payment_method) {
            // eslint-disable-next-line camelcase
            const return_url = window.location.origin + window.location.pathname + '#' + this.$route.fullPath
            console.log(return_url)
            const openid = func.getOpenid()
            http.ajaxPost('order_pay/' + this.id, {payment_method, return_url, openid}, res => {
                alert(JSON.stringify(res))
                if (res.status === 'success') {
                    // eslint-disable-next-line camelcase
                    if (payment_method === 2) {
                        // //支付宝
                        const payDiv = document.getElementById('payDiv')
                        if (payDiv) {
                            document.body.removeChild(payDiv)
                        }
                        const div = document.createElement('div')
                        div.id = 'payDiv'
                        div.innerHTML = res
                        document.body.appendChild(div)
                        document.getElementById('payDiv').getElementsByTagName('form')[0].submit()
                    } else {
                        this.inWeixinCallpay(res.data.wexin_parames)
                    }
                } else {
                    Toast(res.message)
                }
            })
        },
        onFind () {
            http.ajaxGet('order/' + this.id + '/find', {}, res => {

            })
        },
        // 支付调起
        inWeixin (jsonstr) { // 微信上支付
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    'appId': jsonstr.appId, // 公众号名称，由商户传入
                    'timeStamp': jsonstr.timeStamp, // 时间戳，自1970年以来的秒数
                    'nonceStr': jsonstr.nonceStr, // 随机串
                    'package': jsonstr.package, // 预支付交易码
                    'signType': jsonstr.signType, // 微信签名方式：
                    'paySign': jsonstr.paySign // 微信签名
                },
                function (res) {
                    if (res.err_msg === 'get_brand_wcpay_request:ok') {
                        // 使用以上方式判断前端返回,微信团队郑重提示：
                        // res.err_msg将在用户支付成功后返回
                        // ok，但并不保证它绝对可靠。
                    } else {
                        // swal("支付略有延迟，若支付失败，可稍后刷新订单页面查看", {button: {"text": "确定"}})
                        // .then((value) => {
                        //     location.reload();
                        // });
                    }
                }
            )
        },
        inWeixinCallpay (jsonstr) {
            if (typeof WeixinJSBridge === 'undefined') {
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', this.inWeixin(jsonstr), false)
                } else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', this.inWeixin(jsonstr))
                    document.attachEvent('onWeixinJSBridgeReady', this.inWeixin(jsonstr))
                }
            } else {
                this.inWeixin(jsonstr)
            }
        },
        outWeixin (jsonstr) { // 手机浏览器微信支付
            window.location.href = jsonstr.mweb_url
        },
        pcWeixin (basecode) {
            // "data:image/png;base64, '+basecode+'"
        }
    },
    mounted () {
        console.log(this.$route.params)
        const params = this.$route.params
        if (!params.id) {

        } else {
            this.id = params.id
            this.fetch()
        }
    }
}
</script>



