<template>
  <div class="loading-wrap" v-if="loading">
      <van-loading size="24px">加载中...</van-loading>
  </div>
</template>

<script>
export default {
  props: ['loading']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loading-wrap {text-align: center; padding: 20px 0;}
</style>



